import styled from 'styled-components';

export const SociaMedia= styled.div`
    height: auto;
    overflow: hidden
`

export const SociaDiv  = styled.div`
    width: 33.33%;
    float: left;
    box-sizing: border-box;
    padding: 100px 0 100px 60px;

    background: ${props => props.x==="face" ? 'rgb(0,0,0)' : ''};
    background: ${props => props.x==="twitter" ? 'rgb(10,0,0)' : ''};
    background: ${props => props.x==="pin" ? 'rgb(20,0,0)' : ''};
`



export const Icon = styled.i `
    width: 50px;
    height: 50px;
    background: #fff;
    text-align: center;
    line-height: 50px;
    color: #888;
    float: left;
    margin-right: 10px
`

export const SocialP = styled.p`
    font-weight: bold;
    text-transform: uppercase;
    font-size: 20px;
    color: #fff;
`

export const Info1 = styled.span`
    display: block;
    margin-bottom: 8px
`

export const Info2 = styled.span` 
    font-weight: normal
`
